.title {
  * {
    hyphens: none;
  }
}

.title {
  page: title;
  /* color: yellow; */

  h2 {
    margin-top: calc(var(--font-lineHeight) * 3);
    margin-left: 0;
    text-indent: 0;
    font-style: italic;
    font-size: 40px;
    line-height: calc(var(--font-lineHeight) * 2.3);
  }
  p {
    text-indent: 0;
    line-height: var(--font-lineHeight);
  }
  .author {
    font-size: 1em;
    text-indent: 0;
    text-transform: uppercase;
  }
  .author + p {
    margin-top: calc(var(--font-lineHeight) * 2);
    text-transform: uppercase;
  }

  img {
    /* width: 100%;
      position: relative;
      z-index: 202;
      top: 22px; */
  }
}
