.notes {
    padding-bottom: 0;
    margin: 0;
    margin-left: 20px;
    padding: 0;
    .backLink {
        display: none;
    }
    li {
        /* break-inside: avoid; */
        widows: 2;
        orphans: 2;
    }
}
/* chapter endnotes */

#comp-notes-0 {
    page: note;

    h1.ct {
        &::before {
            content: none;
        }
    }

    li {
        text-align: left;

    }

    ol {
        margin-top: 0;
        margin-bottom: 0;
        padding-top:0;
        padding-bottom: 0;
    }
}

