/* here come some simple hacks for features that are not yet in paged js */



/* .pagedjs_page {
    overflow: hidden;
} */


blockquote[data-split-from] {
    padding-top: calc(var(--font-lineHeight) / 2) !important;
}

.notes[data-split-from] {
    padding-top: unset !important;
}   



  /* hack to get the first nammed left page */

  /* hack to remove the white space to element at the top of the page */
  
  .pagedjs_page_content {
    div section[data-split-from] {
      > h2:first-child {
        margin-top: 0;
      }
  
      > h3:first-child {
        margin-top: 0;
      }
  
      > h4:first-child {
        margin-top: 0;
      }
    }
  }
  
  .pagedjs_pages > .pagedjs_page > .pagedjs_area > div blockquote[data-split-from] {
    margin-top: calc(var(--font-lineHeight) / 2);
  }
  