/* Part */

.body-unnumbered {
  page: unnumbered;
}

@page unnumbered {
}

.body-unnumbered {
  h1 {
    &::before {
      margin-left: 3px;
      content: none;
      position: absolute;
      font-size: 0.3em;
      margin-top: calc(var(--font-lineHeight) * 2 * -1);
      height: 0.4em;
      font-style: normal;
      text-transform: uppercase;
      letter-spacing: 0.3em;
      box-shadow: 0 1px 0 black, -2px 0 0 inset white, 2px 0 0 inset white, -2px 0 0 white, 2px 0 0 white;
      padding-bottom: 0.1em;
    }
    header {
      blockquote cite {
        display: block;
        font-weight: 400;
        margin: 0;
        padding: 0;
      }
    }
  }
}