img {
  width: 100%;

  /* height: calc(var(--font-lineHeight) * 10); */
  /* height: calc(var(--font-lineHeight) * 17); */

  height: auto;
  /* max-height: calc(var(--font-lineHeight) * 10); */

  /* margin-top: calc(var(--font-lineHeight) / 2); */
  margin-bottom: calc(var(--font-lineHeight) / 2);
  object-fit: contain;
}

figure {
  padding: 0;
  margin: 0;
  margin-top: calc(var(--font-lineHeight) / 2);
  margin-bottom: calc(var(--font-lineHeight) / 2);

  & + figure {
    margin-top: calc(var(--font-lineHeight) * 2);
  }

  figcaption p {
    text-align: center;
    /* max-width: 70%; */
    margin: 0 auto;
    font-family: var(--font-sans);
    hyphens: none;
  }
  figcaption + img {
    margin-top: var(--font-lineHeight);
  }
}

.screenshot img {
  width: 100%;
  height: calc(var(--font-lineHeight) * 19);
}


