.appendix {
  --link-background: rgb(240, 240, 240);
  page: appendix;
}

h2 {
  /* string-set: title content(text); */
}

@page appendix :left {
  margin-top: 41px;
  @bottom-right {
    content: string(title);
    text-transform: uppercase;
    font-size: 0.8em;
    font-family: var(--font-sans);
    text-overflow: ellipsis;
    font-style: normal;
  }
}

@page appendix :right {
  margin-top: 41px;
  @bottom-left {
    content: string(title);
    text-transform: uppercase;
    font-size: 0.8em;
    font-family: var(--font-sans);
    text-overflow: ellipsis;
    font-style: normal;
  }
}
