ol, ul {
    margin-left: 40px;
    padding-top: calc(var(--font-lineHeight) / 2);
    padding-bottom: 9px;
}

li {
    text-indent: 0;
    hyphens: auto;
}

ol[data-split-from], ul[data-split-from] {
    /* hack to get the grid back */
    padding-top: calc(var(--font-lineHeight) / 2) !important;
    padding-bottom: 9px;
}

ul {
    list-style-type: none;
    li {
        &::before {
            content: "— ";
            display: block;
            position: absolute;
            margin-left: -20px;
        }
    }
    &.none {
        li {
            &::before {
                content: none;
            }
        }
    }
}

ol {
    /* list-style-type: none; */
    /* counter-reset: ol; */
    li {
        /* counter-increment: ol; */
        &::before {
            /* content: counter(ol) ". "; */
            width: 14px;
            display: block;
            position: absolute;
            margin-left: -25px;
            text-align: right;
            font-variant-numeric: oldstyle-nums;
        }
    }
}

.bibliography-entry {
    text-indent: calc(var(--text-indent) * -1 );
    margin-left: var(--text-indent);
    break-inside: avoid;
    widows: 2;
    orphans: 2; 
    text-align: left;
}

ul ul {
    break-before: avoid;
}