

/*.counter set */


.component-front,
.component-back,
.component-body {
  break-before: page;
}

.start-right {
  break-before: right;
}
.start-left {
  break-before: left;
}

/* running elements  */

.running-left {
  position: running(left);
}
.running-right {
  position: running(right);
}

/* .pagedjs_title_first_page,
.pagedjs_halftitle_first_page {
  position: relative;
} */

@page {
  size: 6in 9in;
  margin-top: 61px;
  margin-bottom: 78px;
  /* bleed:3mm; */
  /* marks: cross crop; */
}


@page :right {
  margin-right: 0.75in;
  margin-left: 0.65in;

  @bottom-right {
    text-align: right;
    content: counter(page);
    text-indent: 0;
    width: 50px;
    letter-spacing: 0.2px;
    word-spacing: 0;
    font-size: 10px;
    font-family: var(--font-sans);
  }

  @bottom-left {
    /* content: string(chaptertitle); */
    content: element(right);
    text-indent: 0;
    font-size: 1em;
    font-style: italic;
    font-size: 0.85em;
    letter-spacing: 0.2px;
    word-spacing: 0;
  }
}

@page :left {
  margin-left: 0.75in;
  margin-right: 0.65in;

  @bottom-left {
    content: counter(page);
    /* padding-left: 39px; */
    width: 50px;
    letter-spacing: 0.2px;
    word-spacing: 0;
    font-size: 10px;
    font-family: var(--font-sans);
  }

  @bottom-right {
    /* content: string(booktitle); */
    content: element(right);
    /* content: string(chaptertitle); */
    text-align: right;
    font-size: 1em;
    font-style: italic;
    font-size: 0.85em;
    letter-spacing: 0.2px;
    word-spacing: 0;
  }
}

@page :blank {
  @bottom-left {
    content: none;
  }

  @bottom-right {
    content: none;
  }
}

