/* toc.css */

/* table of content */

.toc {
  page: toc;
  hyphens: none;
}

@page toc:right {
  /* background: red; */
  @bottom-left {
    content: none;
  }
}
@page toc:left {
  /* background: red; */
  @bottom-right {
    content: none;
  }
}

.toc header {
  min-height: unset;
}

.toc .ct::before {
  content: none;
}

.toc ol,
.toc ul {
  list-style-type: none;
  /* margin-top: calc(var(--font-lineHeight) * 3); */
  padding: 0;
  margin-left: 0;
  width: 97%;
  /* position: absolute; */
  bottom: 0;
  li {
    font-weight: 400;
    font-style: italic;
    position: relative;
    margin-bottom: calc(var(--font-lineHeight) / 3);
    text-align: left;
    em {
      font-style: normal;
    }
    &::before {
      content: ". . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . ";
      text-align-last: justify;
      width: calc(100% - 1em);
      margin-left: 0;
      /* height: 3px; */
      bottom: 0;
      /* background: red; */
      position: absolute;
      color: grey;
    }
  }
  .toc-chapter,
  .toc-unnumbered {
  }

  .toc-back {
    margin-top: var(--font-lineHeight);
  }

  .toc-part {
    font-size: 1.3em;
    margin-top: calc(var(--font-lineHeight) * 2);
    /* margin-top: var(); */
    a {
      font-weight: 600;
      color: var(--primary);
    }
    &::before {
      content: none;
    }
    & a::after {
      content: none;
    }
  }
  a {
    color: black;
    /* background: var(--color-paper); */
    text-decoration: none;
    position: relative;
    padding-right: 0.5ch;
    z-index: 2000;
    display: inline-block;
    /* width: 80%; */
    text-shadow: 1px 1px var(--color-paper), -1px -1px var(--color-paper), -1px 1px var(--color-paper), 1px -1px var(--color-paper), 1px 1px var(--color-paper), -1px -1px var(--color-paper), -1px 1px var(--color-paper), 1px -1px var(--color-paper), 3px 3px var(--color-paper), -3px -3px var(--color-paper), -3px 3px var(--color-paper), 3px -3px var(--color-paper);
    /* padding-right: 3em; */
    &::after {
      content: target-counter(attr(href url), page);
      font-style: normal;
      /* float: right; */
      z-index: 2000;
      /* position: relative; */
      background: var(--color-paper);
      padding-left: 2ch;
      /* left: 95px; */
    }
  }
  .toc-front,
  .toc-back {
    /* color: red; */
    a::after {
      /* left: 99px; */
    }
  }
}

.toc .chapterNum {
  /* position: absolute;
  left: -5ch;
  text-align: right;
  width: 2ch; */
}

.toc-component {
}

.toc-front .toc-component {
}



.toc li {
  padding-left: 0;
  margin-left: 0 ;
}
.toc li a {
  display: flex;
  justify-content: space-between;
  .chapterNum {
    place-self: flex-start;
    text-align: right;
    min-width: 4ch;
    background: var(--color-paper);
    padding-right: 2ch;
    align-self: stretch;

  }
  .name {
    background: white;
    display: flex;
  }
  &:after {
    place-self: flex-end;
    height: 1.1em;
  }
}

.toc li a .chapterNum:empty {
  background: var(--color-paper);

}

.toc [data-split-from] li {
  margin-top: 0;
}