body {
  font-family: var(--font-body);
  font-size: var(--font-size);
  line-height: var(--font-lineHeight);
  color: var(--color-body);
  word-spacing: -0.1px;
  font-variant-ligatures: common-ligatures;
}

ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6 {
  & + p {
    text-indent: 0;
  }
}

ul,
ol,
blockquote,
p {
  /* baseline reset*/
  margin: 0;
  padding: 0;
  orphans: 2;
  widows: 2;
  hyphens: auto;
}

p,
li {
  font-size: var(--font-size);
  line-height: var(--font-lineHeight);
  text-align: justify;
  hyphens: none;
  text-indent: var(--text-indent);

  &.sc-continuous-paragraph {
    text-indent: 0;
  }
}

hr {
  height: var(--font-lineHeight);
  margin: 0;
  padding: 0;
  border: 0;
  margin-top: calc(var(--font-lineHeight));
  margin-bottom: calc(var(--font-lineHeight));
  /* width: 60%; */
  position: relative;
  top: 4px;

  &::before {
    width: 100%;
    position: absolute;
    display: block;
    content: "* * *";
    text-align: center;
    margin: 0;

  }

  & + p {
    text-indent: var(--text-indent);
  }
}

blockquote {
  padding-top: calc(var(--font-lineHeight) / 2);
  line-height: var(--font-lineHeight);
  padding-bottom: calc(var(--font-lineHeight) / 2);
  padding-left: 39px;
  font-style: italic;
  font-weight: 400;

  em {
    font-style: normal;
  
  }

  cite {
    font-style: normal;
    line-height: 0;
    margin-left: 0.5em;
    font-weight: 400;

    &::before {
      content: "[";
      margin-right: 0.5ch;
    }

    &::after {
      content: "]";
      margin-left: 0.5ch;
    }

    em {
      font-style: italic;
      line-height: 0;
    }
  }

  &.sepo {
    white-space: pre-wrap;
  }

  p:first-of-type {
    text-indent: 0;
  }
}
.component-front,
.component-body,
.component-back {
  a {
    text-decoration: none;
    font-style: italic;
    line-height: 0;
    /* color:  #E65A3A; */

    &:not(.note-callout) {
      background: linear-gradient(to bottom, var(--link-background) 0, var(--link-background) 95%, var(--color-secondary) 95%, var(--color-secondary) 100%);
      color: var(--color-secondary);

      &::after {
        /* content: " — " attr(href); */
        font-size: 0.9em;
        line-height: 0;
        /* white-space: pre-wrap; */
        /* word-break: break-all; */
      }
    }
  }
}

a.note-callout {
  /* background: transparent; */
  font-style: normal;
  font-weight: 600;
  font-variant-numeric: oldstyle-nums;
  font-size: 0.75em;
  margin-left: 0.2ch;
  margin-right: 0.3ch;
  position: relative;
  top: -3px;
  color: inherit;
  font-weight: 400;
  line-height: 0;
  /* vertical-align: top; */
  font-variant-numeric: lining-nums;
  font-family: var(--font-sans);
}

em,
sup,
sub,
strong,
b,
i {
  line-height: 0;
}

em {

}
.small-caps {
  /* font-variant: all-small-caps; */
  font-family: var(--font-body-small-caps);
  text-transform: lowercase;
}

figure {
  figcaption {
    p {
      text-indent: 0;
    }
  }
}

.underline {
  text-decoration: underline;
}

.notes {
  /* margin-top: 0;
  margin-bottom: 0;
  padding-top: 0; */

  li {
    /* break-inside: avoid; */
  }
}

ol.notes[data-split-from],
ul.notes[data-split-from] {
  padding-top: 0 !important;
}

/* avoid one note single on a page */
[id^="note-"]:nth-last-child(2) {
  break-after: avoid;
}

.contrib {
  margin-bottom: var(--font-lineHeight);
  text-indent: 0;
  strong {
    font-variant: all-small-caps;
    font-weight: bold;
    font-size: 1.3em;
  }

}

p.contrib.start:first-letter {
  all: unset;
}

p.contrib.start:first-line {
  all: unset;
}

/* japan text */


span[lang="ja"] {
  font-style: normal;
}

.jap-in {
  text-justify: inter-word;

}


.emdash {
  letter-spacing: 1px;
}


.unnumbered-notes, .part-notes, .component-notes {
  li {
    text-align: left;
  }
}