
.block-appendix {
  background: rgb(240, 240, 240);
  box-shadow: 0 0 0 4px rgb(220, 220, 220);
  padding: 1em;
  margin-bottom: var(--font-lineHeight);
  padding-bottom: 0;
  h2 {
    text-align: center;
    font-style: normal;
    font-size: 15px;
  }
  ol,
  ul {
    margin-left: 2em;
  }
}

.appendix,
.block-appendix {
 
  word-spacing: unset;
  font-family: var(--font-sans);

  h2,
  h3,
  h4 {
    color: black;
  }
  header {
    /* display: none; */
    min-height: unset;
    h2 {
      line-height: var(--font-lineHeight);
      text-align: center;
      font-style: normal;
      font-size: 15px; 
    }
  }
  h3 {
    margin-bottom: 0.5em;
    font-size: 1.3em;
    font-style: normal;
  }
  .container {
    /* border-radius: 8px; */
    /* padding: 0 2em; */

    * {
      text-indent: 0;
      text-align: left;
    }
    a {
      text-decoration: none;
      /* color: inherit; */
      color: black;
    }
    table {
      width: 100%;
      table-layout: fixed;
      border-collapse: collapse;
      margin-top: 2em;
      margin-bottom: 2em;
      font-size: 0.9em;
      line-height: 14px;

      p,
      li {
        font-size: 1em;
        text-align: left;
        text-indent: 0;
        line-height: 1.2;
      }

      blockquote {
        margin: 0;
        padding: 0;
      }

      tr {
        break-inside: avoid;
      }
      td,
      th {
        border: 1px solid grey;
        padding: 0.5em;
        a {
          word-break: break-all;
          

        }
      }
    }

  
  }

  figcaption {
    font-family: var(--font-body);
  }
}
.appendix {
  .rotate {
    /* transform: rotate(90deg); */
    writing-mode: vertical-rl;
    /* text-orientation: mixed; */
    /* position: absolute; */
    /* transform: scale(0.9) translateY(-70px); */
    /* height: var(--pagedjs-height); */
    table {
      margin: 0;
      margin-top: 0;
      margin-bottom: 0;
      td {
        vertical-align: top;
      }
    }
  }
}
