.cover {
    page: cover;
    header {
        /* display: none; */
    }
    figure {
        position: absolute;
        top: -10px;
        right: -10px;
        left: -10px;
        bottom: -10px;
        margin: 0;
        img {
            width: 100%;
            height: 100%;
            margin: 0;
            object-fit: cover;
        }
    }
}

@page cover {
    margin: 0px;
    @bottom-left {
        content: none;
    }
    @bottom-right {
        content: none;
    }
}