h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  margin: 0;
  padding: 0;
  color: var(--primary);
}

header {
  /* background: red; */
}

.part-number,
.chapter-number {
  margin-left: 3px;
  margin-top: calc(var(--font-lineHeight) * 2 * -1);
  border-top: 1px solid var(--primary);
  height: 0.4em;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  padding-bottom: 0.1em;
  color: var(--color-body);
  font-family: var(--font-sans);
}

.part-number::before {
  content: "part ";
}

.chapter-number::before {
  content: "chapter ";
}

h1 {
  text-align: left;
  font-size: 30px;
  line-height: calc(var(--font-lineHeight) * 2);
  max-width: 20ch;
  line-height: calc(var(--font-lineHeight) * 2);

  &.ct,
  &.booktitle {
    margin-top: calc(var(--font-lineHeight) * 3);
    margin-left: 0;
    text-indent: 0;
    font-style: italic;
  }

  &.booktitle::before {
    content: none;
  }

  & + p {
    /* margin if there is no epigraph */
    /* margin-top: calc(var(--font-lineHeight) * 8 - 1px); */
    /* text-indent: 40px; */
  }
}

header {
  min-height: calc(var(--font-lineHeight) * 12);
}

:not(.appendix),
:not(.appendix),
:not(.contributors) {
  header + p.double-story {
    &:first-letter {
      /* margin-bottom: 10px; */
    }
  }
}

.component-front,
.component-body {
  header + p {
    &:first-line {
      font-family: var(--font-body-small-caps);
      /* font-variant: all-small-caps;      */
      /* font-feature-settings: 'c2sc' on, 'smcp' on;     */
      /* text-transform: uppercase; */
      /* font-weight: 400; */
      /* letter-spacing: 0.2em; */
    }

    &::first-letter {
      font-family: var(--font-body);
      font-size: 7em;
      position: relative;
      color: var(--primary);
      font-style: italic;
      font-weight: 600;
      text-transform: uppercase;
      line-height: 0.8;
      display: inline-block;
      font-variant: none;
      float: left;
      margin-left: -16px;
      border: 2px solid #0000;
      margin-right: 14px;
      margin-top: -20px;
      /* margin-bottom: 0; */
    }
  }
}

h2 + p {
  text-indent: 0;
}

