:root {
  --font-scale: 1.618;
}

h1 {
  /* font-size: calc(var(--font-scale) * var(--font-size) * 1.8); */
  line-height: calc(var(--font-lineHeight) * 2);
}

h2 {
  font-size: 20px;
  line-height: calc(var(--font-lineHeight) * 1);
  font-style: italic;
  margin-top: calc(var(--font-lineHeight) * 2);
  margin-bottom: calc(var(--font-lineHeight) * 1);
  /* max-width: 65%; */
  & + p {
    /* text-indent: 0; */
  }

  &.notes-title {
    margin-top: calc(var(--font-lineHeight) * 2);
  }
}

h3 {
  font-size: 16px;
  /* margin-left: 39px; */
  margin-top: calc(var(--font-lineHeight) * 2);
  margin-bottom: calc(var(--font-lineHeight) * 1);
  font-style: italic;

  & + p {
    /* text-indent: 40px; */
  }
}

h4 {
  font-size: 14px;
  /* margin-left: 39px; */
  margin-bottom: 0;
  color: var(--primary);
  margin-top: calc(var(--font-lineHeight) * 1);

  & + p {
    /* text-indent: 40px; */
  }
}

h5 {
  font-size: var(--font-size);
  font-weight: 600;
  margin-top: var(--font-lineHeight);
  color: var(--color-body);
}

h6 {
  font-size: var(--font-size);
  font-weight: 400;
  margin-top: calc(var(--font-lineHeight) / 2);
  margin-bottom: calc(var(--font-lineHeight) / 2);
  margin-left: var(--text-indent);
  color: var(--color-body);

  & + p {
    text-indent: var(--text-indent);
  }
}

/* tester */

.pagedjs_page_content::after {
  content: " ";
  display: block;
  position: absolute;
  width: 1px;
  top: -40000px;
  bottom: -40000px;
  left: 39px;
  /* background: var(--color-ruler); */
  z-index: 4000;
}

.cst {
  margin-top: calc(var(--font-lineHeight) * 1);
  text-indent: 0;
  font-size: 15px;
  font-style: italic;
  font-weight: 600;
  max-width: 80%;
  hyphens: none;
  text-align: left;
  color: var(--primary);
  margin-bottom: 0;

  &:last-child {
    margin-bottom: calc(var(--font-lineHeight) * 6);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  break-after: avoid;
}

header blockquote:last-child {
  margin-bottom: 72px;
}



header {
  cite {
    display: block;
    margin-top: calc(var(--font-lineHeight) - 1px);
    line-height: 18px;
    font-weight: 400;
    text-align: left;
    /* width: 100%; */
    padding-left: 12em;
    /* margin-top: 6em; */
    /* margin-left: 40px; */
    &::before {
      /* content: "▸ " */
      content: "\2014";
      margin-right: 1ch;
      font-size: 0.85em;
    }

    &::after {
      content: none;
    }
  }

  .sep,
  .sepo {
    background: rgba(255, 255, 255, 0.3);
    padding: var(--font-lineHeight);
    /* padding-left: 40px; */
    width: 55ch;
    text-align: left;
  
  }

  .ct + blockquote {
    margin-top: 144px;
  }
}

header {
  .author {
    text-transform: uppercase;
    margin-top: var(--font-lineHeight);
    text-indent: 0;
    hyphens: none;
    text-align: left;
    font-family: var(--font-sans);
  }
}

p.start {
  text-indent: 0;
}

header blockquote.sep {
  width: 100%;
  padding-left: 12em;
  margin-top: 6em;

  p {
    text-indent: 0;
  }
}

.break {
  break-after: page;
}

.front-component,
.back-component {
  h1::before {
    content: none;
  }
}

/* to update when editoria include component number */
.chapter {
  header {
    blockquote cite {
      display: block;
      font-weight: 400;
      margin: 0;
      padding: 0;
    }
  }
}

.half-title {
  h1 {
    color: white;
    margin-top: calc(var(--font-lineHeight) * 3);
    margin-left: 0;
    text-indent: 0;
    font-style: italic;
  }
}