.table {
  table {
    border-collapse: collapse;
    /* border: 1px solid black; */
    font-size: 0.85em;
    font-family: var(--font-sans);
    line-height: 1.2;
    table-layout: fixed;
    margin-bottom: var(--font-lineHeight);
    width: 100%;
    tr {
      break-inside: avoid;
    }
    td,
    th {
      border: 1px solid black;
      padding: 0.3em;
    vertical-align: inherit;

    }
    th {
      background: rgb(230, 230, 230);
    }
    figcaption {
      margin-top: var(--font-lineHeight);
    }
    p,
    li {
      font-size: 1em;
      text-align: left;
      text-indent: 0;
      line-height: 1.2;
    }
    li p {
      font-size: 1em;
    }
    ul {
      margin-left: 22px;
    }
    p + ul {
      margin-top: 0;
      padding-top: 0;
    }
  }
}

th:empty {
  border: 0;
  background: unset;
}
